<template>
    <div id="edit-user">
        <h3>Editar usuario</h3>
        <div class="row">
          <form @submit.prevent="updateUsuario" class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <input disabled type="text" v-model="usuario_id" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="nombre" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="apellido" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="celular" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="correo" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="password" v-model="password" required>
              </div>
            </div>
            <button type="submit" class="btn">Enviar</button>
            <router-link to="/" class="btn grey">Cancelar</router-link>
          </form>
        </div>
    </div>
    </div>
</template>

<script>
import db from "./firebaseInit.js";
export default {
  name: "edit-user",
  data() {
    return {
      usuario_id: null,
      nombre: null,
      apellido: null,
      celular: null,
      correo: null,
      password: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("usuarios")
      .where("usuario_id", "==", to.params.usuario_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.usuario_id = doc.data().usuario_id;
            vm.nombre = doc.data().nombre;
            vm.apellido = doc.data().apellido;
            vm.celular = doc.data().celular;
            vm.correo = doc.data().correo;
            vm.password = doc.data().password;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db.collection("usuarios")
        .where("usuario_id", "==", this.$route.params.usuario_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.usuario_id = doc.data().usuario_id;
            this.nombre = doc.data().nombre;
            this.apellido = doc.data().apellido;
            this.celular = doc.data().celular;
            this.correo = doc.data().correo;
            this.password = doc.data().password;
          });
        });
    },
    updateUsuario() {
      db.collection("usuarios")
        .where("usuario_id", "==", this.$route.params.usuario_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref
              .update({
                usuario_id: this.usuario_id,
                nombre: this.nombre,
                apellido: this.apellido,
                celular: this.celular,
                correo: this.correo,
                password: this.password
              })
              .then(() => {
                this.$router.push({
                  name: "view-user",
                  params: { usuario_id: this.usuario_id }
                });
              });
          });
        });
    }
  }
};
</script>



