<template>
    <div id="new-user">
        <h3>Nuevo Usuario</h3>
        <div class="row">
          <form @submit.prevent="saveUsuario" class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="usuario_id" required>
                <label>ID del usuario</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="nombre" required>
                <label>Nombre</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="apellido" required>
                <label>Apellido</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="celular" required>
                <label>Número telefónico</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="correo" required>
                <label>Correo electrónico</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="password" v-model="password" required>
                <label>Contraseña</label>
              </div>
            </div>
            <button type="submit" class="btn">Enviar</button>
            <router-link to="/" class="btn grey">Cancelar</router-link>
          </form>
        </div>
    </div>
</template>

<script>
import db from "./firebaseInit.js";
export default {
  name: "new-user",
  data() {
    return {
      usuario_id: null,
      nombre: null,
      apellido: null,
      celular: null,
      correo: null,
      password: null
    };
  },
  methods: {
    saveUsuario() {
      db.collection("usuarios")
        .add({
          usuario_id: this.usuario_id,
          nombre: this.nombre,
          apellido: this.apellido,
          celular: this.celular,
          correo: this.correo,
          password: this.password
        })
        .then(docRef => this.$router.push("/"))
        .catch(error => console.log(err));
    }
  }
};
</script>



