<template>
    <div id="view-user">
        <ul class="collection with-header">
          <li class="collection-header"><h4>{{nombre}} {{apellido}}</h4></li>
          <li class="collection-item">ID del usuario: {{usuario_id}}</li>
          <li class="collection-item">Número Telefónico: {{celular}}</li>
          <li class="collection-item">Correo electrónico: {{correo}}</li>
          <!--<li class="collection-item">Contraseña: {{password}}</li>-->
        </ul>
        <router-link to="/" class="btn grey">Atrás</router-link>
        <button @click="deleteUsuario" class="btn red">Borrar</button>
        <div class="fixed-action-btn">
          <router-link v-bind:to="{name: 'edit-user', params: {usuario_id: usuario_id}}" class="btn-floating btn-large red">
            <i class="fa fa-pencil"></i>
          </router-link>
        </div>
    </div>
</template>

<script>
import db from "./firebaseInit.js";
export default {
  name: "view-user",
  data() {
    return {
      usuario_id: null,
      nombre: null,
      apellido: null,
      celular: null,
      correo: null,
      password: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("usuarios")
      .where("usuario_id", "==", to.params.usuario_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.usuario_id = doc.data().usuario_id;
            vm.nombre = doc.data().nombre;
            vm.apellido = doc.data().apellido;
            vm.celular = doc.data().celular;
            vm.correo = doc.data().correo;
            vm.password = doc.data().password;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db.collection("usuarios")
        .where("usuario_id", "==", this.$route.params.usuario_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.usuario_id = doc.data().usuario_id;
            this.nombre = doc.data().nombre;
            this.apellido = doc.data().apellido;
            this.celular = doc.data().celular;
            this.correo = doc.data().correo;
            this.password = doc.data().password;
          });
        });
    },
    deleteUsuario() {
      if (confirm("Estás seguro?")) {
        db.collection("usuarios")
          .where("usuario_id", "==", this.$route.params.usuario_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete();
              this.$router.push("/"); //Para redirigir uso $router
            });
          });
      }
    }
  }
};
</script>



