<template>
    <div id="dashboard">
        <ul class="collection with-header">
          <li class="collection-header"><h4>Usuarios</h4></li>
          <li v-for="usuario in usuarios" v-bind:key="usuario.id" class="collection-item">
            <div class="chip">{{usuario.correo}}</div>{{usuario.usuario_id}}: {{usuario.nombre}} {{usuario.apellido}}
          
          <router-link class="secondary-content" v-bind:to="{name: 'view-user', params: {usuario_id: usuario.usuario_id}}">
            <i class="fa fa-eye"></i>
          </router-link>
          </li>
        </ul>

        <div class="fixed-action-btn">
          <router-link to="/new" class="btn-floating btn-large blue">
            <i class="fa fa-plus"></i>
          </router-link>
        </div>
    </div>
</template>

<script>
import db from "./firebaseInit.js";
export default {
  name: "dashboard",
  data() {
    return {
      usuarios: []
    };
  },
  created() {
    db.collection("usuarios")
      .orderBy("usuario_id")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          console.log(doc.id);
          const data = {
            id: doc.id,
            usuario_id: doc.data().usuario_id,
            nombre: doc.data().nombre,
            apellido: doc.data().apellido,
            celular: doc.data().celular,
            correo: doc.data().correo,
            password: doc.data().password
          };
          this.usuarios.push(data);
        });
      });
  }
};
</script>



